import React, { forwardRef, Fragment, useEffect } from 'react';
import { bool, func, node, oneOf, string } from 'prop-types';
import classnames from 'classnames';
import ReactAriaModal from 'react-aria-modal';
import DialogCard from '../dialog-card';

const CSS_PREFIX = 'vds-dialog';
const CSS_STATE = 'vds-state';

const Dialog = forwardRef(({
  children,
  className,
  css,
  includeDefaultStyles,
  open,
  scrollDisabled,
  type,
  underlayClass,
  underlayColor,
  ...remainingProps
}, ref) => {

  useEffect(() => {
    const html = document.documentElement;
    if (open) {
      return html.classList.add('vds-dialog-no-scroll');
    }
    return html.classList.remove('vds-dialog-no-scroll');
  }, [open]);

  return (
    <Fragment>
      {
        open
          ? <ReactAriaModal
            css={css}
            dialogClass={classnames(CSS_PREFIX, {
                [`${CSS_STATE}--show`]: true,
                [`${CSS_STATE}--${type}`]: Boolean(type)
              }, className)}
            includeDefaultStyles={includeDefaultStyles}
            ref={ref}
            scrollDisabled={scrollDisabled}
            underlayClass={underlayClass}
            underlayColor={underlayColor}
            {...remainingProps}
          >
            <DialogCard>
              {children}
            </DialogCard>
          </ReactAriaModal>
          : undefined
      }
    </Fragment>
  );
});

Dialog.propTypes = {
  /**
  * @ignore
  */
  children: node,

  /**
   * @ignore
   */
  className: string,

  /**
   * @ignore
   */
  css: func,

  /**
   * Include default styles
   */
  includeDefaultStyles: bool,

  /**
   * Show dialog
   */
  open: bool,

  /**
   * Scroll disabled
   */
  scrollDisabled: bool,

  /**
   * Dialog Type
   */
  type: oneOf(['error', 'success', 'warning']),

  /**
   * Dialog underlay class
   */
  underlayClass: string,

  /**
   * Dialog underlay color
   */
  underlayColor: bool
};

Dialog.defaultProps = {
  includeDefaultStyles: false,
  scrollDisabled: false,
  underlayClass: `${CSS_PREFIX}-curtain ${CSS_STATE}--show`,
  underlayColor: false
};

Dialog.displayName = 'Dialog';

export default Dialog;
