import React, { forwardRef } from 'react';
import { node, string } from 'prop-types';
import classnames from 'classnames';

const CSS_PREFIX = 'vds-dialog-card--head-icon';

const DialogCardHeadIcon = forwardRef(({ children, className, ...remainingProps }, ref) => (
  <span
    className={classnames(CSS_PREFIX, className)}
    ref={ref}
    {...remainingProps}
  >
    {children}
  </span>
));

DialogCardHeadIcon.propTypes = {
  /**
  * @ignore
  */
  children: node,

  /**
   * @ignore
   */
  className: string
};

DialogCardHeadIcon.displayName = 'DialogCardHeadIcon';

export default DialogCardHeadIcon;
