import React, { forwardRef } from 'react';
import { node, string } from 'prop-types';
import classnames from 'classnames';

const CSS_PREFIX = 'vds-dialog-card';

const DialogCard = forwardRef(({
  children,
  className,
  role,
  ...remainingProps
}, ref) => (
  <div
    className={classnames(CSS_PREFIX, className)}
    ref={ref}
    role={role}
    {...remainingProps}
  >
    {children}
  </div>
));

DialogCard.propTypes = {
  /**
  * @ignore
  */
  children: node,

  /**
   * @ignore
   */
  className: string,

  /**
   * Role
   */
  role: string
};

DialogCard.displayName = 'DialogCard';

export default DialogCard;
