import React, { forwardRef } from 'react';
import { elementType, node, string } from 'prop-types';
import classnames from 'classnames';

const CSS_PREFIX = 'vds-dialog-card--head-title';

const DialogCardHeadTitle = forwardRef(({ children, className, tag: Tag, ...remainingProps }, ref) => (
  <Tag
    className={classnames(CSS_PREFIX, className)}
    ref={ref}
    {...remainingProps}
  >
    {children}
  </Tag>
));

DialogCardHeadTitle.propTypes = {
  /**
  * @ignore
  */
  children: node,

  /**
   * @ignore
   */
  className: string,

  /**
  * Tag
  */
  tag: elementType
};

DialogCardHeadTitle.defaultProps = {
  tag: 'h2'
};


DialogCardHeadTitle.displayName = 'DialogCardHeadTitle';

export default DialogCardHeadTitle;
