import React, { forwardRef } from 'react';
import { node, string } from 'prop-types';
import classnames from 'classnames';

const CSS_PREFIX = 'vds-dialog-card--close';

const DialogCardClose = forwardRef(({ children, className, ...remainingProps }, ref) => (
  <div
    className={classnames(CSS_PREFIX, className)}
    ref={ref}
    {...remainingProps}
  >
    {children}
  </div>
));

DialogCardClose.propTypes = {
  /**
  * @ignore
  */
  children: node,

  /**
   * @ignore
   */
  className: string
};

DialogCardClose.displayName = 'DialogCardClose';

export default DialogCardClose;
