import React, { forwardRef } from 'react';
import { node, string } from 'prop-types';
import classnames from 'classnames';

const CSS_PREFIX = 'vds-dialog-subtitle';

const DialogSubtitle = forwardRef(({ children, className, ...remainingProps }, ref) => (
  <span
    className={classnames(CSS_PREFIX, className)}
    ref={ref}
    {...remainingProps}
  >
    {children}
  </span>
));

DialogSubtitle.propTypes = {
  /**
  * @ignore
  */
  children: node,

  /**
   * @ignore
   */
  className: string
};

DialogSubtitle.displayName = 'DialogSubtitle';

export default DialogSubtitle;
